body{
    background-image: url("../Resources/bgd.jpg");
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-position: center;
    background-color: #111111;
}

.container {
    display: flex;
    flex-direction: column;
    margin: 10px 0 20px 10px;
    background-color: rgba(0, 0, 0, 0.8);
    border-radius: 5px;
}
.tapster{
 padding-top: 10px;
}
.tapster p {
    color: #0d89bc;
}
.tapster a{
    color: white;
}
.ag{
    align-items: center;
    flex: 1fr;
}
.ag p{
    color: #0d89bc;
}
.discord{
    margin-bottom: 200px;
}
.discord p{
    color: #0d89bc;
}
.discord a{
    color: white;
}
.text{
    background: rgb(5,75,203);
    background: linear-gradient(54deg, rgba(5,75,203,1) 11%, rgba(6,226,208,1) 47%, rgba(0,235,35,1) 73%, rgba(0,163,0,1) 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    font-size: 70px;
    padding-bottom: 20px;
}
.title-text{
    font-weight: bold;
    font-size: 40px;
    color: white;
    align-self: flex-start;
    padding-bottom: 15px;
}
.h3-title-text{
    font-weight: bold;
    font-size: 30px;
    color: white;
    align-self: flex-start;
    padding-bottom: 15px;
}
.image-container{
    padding-top: 10px;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}
.image{
    max-width: 100%;
    height: auto;
    margin: 5px;
}

@media screen and (max-width: 400px){
    .container {
        display: flex;
        flex-direction: column;
        margin: 10px 0 20px 0;
        background-color: rgba(0, 0, 0, 0.8);
        border-radius: 5px;
    }
}