body{
    background-image: url("../Resources/bgd.jpg");
    background-repeat: no-repeat;
    background-attachment: fixed;
    background-position: center;
    background-color: #111111;
}
.name-container{
    text-align: left;
    padding-top: 40px;
}
.name{
    color: white;
    font-weight: bolder;
    font-size: 55px;
    padding-left: 90px;
}
.text-container{
    text-align: left;
    padding-top: 20px;
    width: 600px;
    padding-left: 90px;
}
.p-text{
    color: #61dafb;
    font-size: 20px;
}
@media screen
and (max-device-width: 800px) {
    .name-container{
        text-align: left;
        padding-top: 40px;
    }
    .name{
        color: white;
        font-weight: bolder;
        font-size: 35px;
        padding-left: 40px;
    }
    .text-container{
        text-align: left;
        padding-top: 20px;
        width: 300px;
        padding-left: 40px;
    }
    .p-text{
        color: #61dafb;
        font-size: 15px;
    }
}